@font-face {
  font-family: 'iconfont';  /* project id 391292 */
  src: url('//at.alicdn.com/t/font_391292_w759mxs1yqp06bt9.eot');
  src: url('//at.alicdn.com/t/font_391292_w759mxs1yqp06bt9.eot?#iefix') format('embedded-opentype'),
  url('//at.alicdn.com/t/font_391292_w759mxs1yqp06bt9.woff') format('woff'),
  url('//at.alicdn.com/t/font_391292_w759mxs1yqp06bt9.ttf') format('truetype'),
  url('//at.alicdn.com/t/font_391292_w759mxs1yqp06bt9.svg#iconfont') format('svg');
}
.iconfont{
    font-family:"iconfont" !important;
    font-size:.75rem;font-style:normal;
    -webkit-font-smoothing: antialiased;
    -webkit-text-stroke-width: 0.2px;
    -moz-osx-font-smoothing: grayscale;
}
html,body{
  margin: 0 auto;
  height:100%;
  font-family: '微软雅黑'
}
body{
  width: 10rem;
}
h1{
  font-weight: bold !important;
  font-size:.75rem;
}
h2{
  font-weight: bold !important;
  font-size: .5rem;
  text-align: left;
  color: #5B9EE4;
  font-family: '微软雅黑'
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
  width: 10rem;
  height:100%;
  position: relative;
  margin-top: 0 !important;
}
a{
  color: #555;
  font-size: 0.45rem
}
.container{
  /*margin-top: .5rem;*/
}
.wapper{
  position: absolute;
  top: 0;
  left: 0;
  width: 10rem;
  height: 100%;
  transition: all .5s;
}
.active{
  left:0 !important;
  transition: left .5s ;
}
.activeLeft{
  margin-left: 4.5rem;
  transition: all .5s;
}
.child-header{
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999;
  padding: .35rem .1rem;
  width: 9.8rem;
  background:rgba(255,255,255,0);
  transition: all 1s ;
}
.black{
    color: black
}
.white{
    color: white
}
.child-header span{
  font-size: .45rem;
}



